.onSearch{
    background-color: rgb(24,24,24);
    padding-left: 100px;
    color: rgb(255,255,255);
    padding-top: 60px;
}

.onSearch img{
    width: 400px;
    height: 230px;
}

.filter{
    border-bottom: 1px solid gray;
    padding: 15px 0px 8px 0px;
    margin-right: 100px;
    
}

.filter-btn{
    display: flex;
    align-items: center;
    color: rgb(170, 170, 170);
    cursor: pointer;
    width: fit-content;
    transition: all 0.2s ease;
}

.filter-btn:hover{
    color: rgb(241, 241, 241);
}

.filter i{
    font-size: 20px;
    margin-right: 10px;
}

.filter p{
    font-size: 14px;
}

.video-cards a{
    display: flex;
    text-decoration: none;
    margin-right: 100px;
}

.video-info{
    margin-left: 50px;
    position: relative;
    width: 75%;
}

.video-info h4{
    font-size: 18px;
    padding: 5px 0px;
    color: rgb(255,255,255);
}

.video-info p{
    color: rgb(170, 170, 170);
    font-size: 12px;
}

