/* ==================== Search Bar ==================== */


.searchbar{
    position: fixed;
    height: 60px;
    width: 100%;
    background-color: rgb(33, 33, 33);
    color: rgb(231, 231, 231);
    z-index: 1;
    display: flex;
    justify-content: space-between;
}


.yt-opt-btn{
    display: flex;
    flex-direction: row;
    margin: 7px 0px auto 20px;
}

.yt-opt-btn h2{
    padding: 8px 10px;
    margin: 0px;
    text-align: center;
}

.yt-opt-btn h2 i{
    color: red;
    font-size: 26px;
    margin-right: 4px;
}

.yt-opt-btn h2 p{
    display: inline;
}

.yt-opt-btn h2:first-of-type{
    border-radius: 50%;
    width: 45px;
    text-align: center;
    align-items: center;
    cursor: pointer;
    transition: all 0.3s ease-out;
}

.yt-opt-btn h2:first-of-type:active{
    background-color: rgb(55, 55, 55);
}

.yt-opt-btn h2:first-of-type i{
    color: rgb(231, 231, 231);
    font-size: 22px;
    margin-right: 0;
}

.searchbar-inp{
    margin-top: 15px;
}

.searchbar-inp input{
    height: 30px;
    width: 600px;
    background-color: rgb(18,18,18);
    border: none;
    color: rgb(231, 231, 231);
    font-size: 16px;
    padding-left: 10px;
}

.searchbar-inp input:focus{
    outline: solid 1px rgb(27, 115, 231);
    outline-offset: -1px;
}
  
input[type="search"].dark {
    color: #fff;
}

input[type="search"]::-webkit-search-cancel-button {
    -webkit-appearance: none;
    height: 1em;
    width: 1em;
    border-radius: 50em;
    background: url(https://pro.fontawesome.com/releases/v5.10.0/svgs/solid/times-circle.svg) no-repeat 50% 50%;
    background-size: contain;
    opacity: 0;
    pointer-events: none;
}
  
input[type="search"]:focus::-webkit-search-cancel-button {
    opacity: .3;
    pointer-events: all;
    margin-right: 5px;
}
  
input[type="search"].dark::-webkit-search-cancel-button {
    filter: invert(1);
}

.searchbar-inp button{
    height: 30px;
    width: 70px;
    background-color: rgb(49,49,49);
    color: rgb(231, 231, 231);
    border: none;
    font-size: 16px;
    cursor: pointer;
}

.searchbar-inp button:hover{
    color: white;
}

.user-opt{
    margin-top: 10px;
}

.user-opt ul{
    display: flex;
    flex-direction: row;
    list-style-type: none;
}

.user-opt ul li{
    padding: 10px 12px;
    margin: 0px 8px;
    border-radius: 50%;
    font-size: 20px;
    cursor: pointer;
    transition: all 0.3s ease-out;
}

.user-opt ul li:active{
    background-color: rgb(76,76,76);
}

/* ++++++++++++ Display None +++++++++++++ */

.displaynone{
    display: none;
}

/* ++++++++++++ User Dropdown ++++++++++++ */

.user-dropdown{
    position: absolute;
    right: 50px;
    top: 10px;
    bottom: 0;
    width: 300px;
    background-color: rgb(33, 33, 33);
    border-bottom: 1px solid gray;
    border-left: 1px solid gray;
    border-right: 1px solid gray;
    z-index: 2;
}

/* ++++++++++++ Notification Dropdown +++++++++++++ */

.notifications-dropdown{
    position: absolute;
    right: 110px;
    top: 10px;
    bottom: 0;
    width: 400px;
    background-color: rgb(33, 33, 33);
    border-bottom: 1px solid gray;
    border-left: 1px solid gray;
    border-right: 1px solid gray;
    z-index: 2;
}

.notifications-dropdown h4{
    color: rgb(231, 231, 231);
    font-weight: 400;
    padding: 15px 15px;
    border-bottom: 1px solid rgb(231, 231, 231);
}

.notifications-dropdown h4 i{
    font-size: 20px;
    float: right;
    cursor: pointer;
}

/* +++++++++++++++ Yt apps Dropdown ++++++++++++++ */

.ytapps-dropdown{
    position: absolute;
    right: 140px;
    top: 50px;
    width: 240px;
    background-color: rgb(33, 33, 33);
    border-bottom: 1px solid gray;
    border-left: 1px solid gray;
    border-right: 1px solid gray;
    z-index: 2;
}

.ytapps-dropdown ul{
    list-style-type: none;
    padding: 10px 0px;
}

.ytapps-dropdown ul li i{
    color: red;
    font-size: 18px;
    width: 40px;
}

.ytapps-dropdown ul li img{
    margin-bottom: -6px;
    margin-right: 20px;
    width: 22px;
    height: 22px;
}

.ytapps-dropdown ul li{
    padding: 10px 20px;
    color: rgb(231, 231, 231);
    cursor: pointer;
}

.ytapps-dropdown ul li:hover{
    background-color: rgb(76,76,76);
}

/* +++++++++++++++ Create Dropdown ++++++++++++++++ */

.create-dropdown{
    position: absolute;
    right: 30px;
    top: 50px;
    width: 180px;
    background-color: rgb(33, 33, 33);
    border-bottom: 1px solid gray;
    border-left: 1px solid gray;
    border-right: 1px solid gray;
    z-index: 2;
}

.create-dropdown ul{
    list-style-type: none;
    padding: 10px 0px;
}

.create-dropdown ul li i{
    width: 40px;
}

.create-dropdown ul li{
    padding: 10px 20px;
    color: rgb(231, 231, 231);
    cursor: pointer;
}

.create-dropdown ul li:hover{
    background-color: rgb(76,76,76);
}

/* =============================== Side Bar ========================== */


.bg-overlay{
    position: absolute;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
    background-color: rgba(0, 0, 0, 0.7);
    z-index: 2;
}
.bg-overlay2{
    position: absolute;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
    background-color: rgba(0, 0, 0, 0);
    z-index: 0;
}

.sidebar{
    position: fixed;
    top: 0;
    left: 0px;
    width: 240px;
    background-color: rgb(33, 33, 33);
    color: rgb(231, 231, 231);
    transition: all 0.5s ease-out;
    z-index: 2;
}

.hidden-sidebar{
    position: fixed;
    top: 0;
    left: -238px;
    transition: all 0.5s ease;
}

.sidebar-section1{
    border-bottom: 1px solid gray;
    padding: 10px 0px;
    margin-top: 10px;
}

.sidebar-section2{
    border-bottom: 1px solid gray;
    padding: 10px 0px;
}

.sidebar-section1 ul,.sidebar-section2 ul{
    list-style-type: none;
}

.sidebar-section1 ul li,.sidebar-section2 ul li{
    padding: 12px 0px 12px 30px;
    font-size: 14px;
    cursor: pointer;
}

.sidebar-section1 ul li:hover,.sidebar-section2 ul li:hover{
    background-color: rgb(76,76,76);
}

.sidebar-section1 ul li i,.sidebar-section2 ul li i{
    font-size: 18px;
    width: 42px;
}
